body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
    color: #fff;
    overflow-x: hidden;
  }
  
  .app {
    position: relative;
    min-height: 100vh;
    z-index: 1;
  }
  
  canvas {
    width: 100%;
    height: 100%;
  }